
import Countup from 'vux/src/components/countup/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import Group from '../components/User/Group'
import Cell from '../components/User/Cell'
import { mapActions, mapGetters } from 'vuex'
import Levels from '@/assets/level/level-show'
export default {
data () {
return {
Levels: Levels,
shareMaskVisible: false
}
},
components: {
Countup,
Group,
Cell,
XDialog
},
beforeRouteEnter (to, from, next) {
document.title = '个人中心'
next()
},
created () {
this.$vux.loading.show({
text: '正在加载'
})
this.getUserInfo().then((res) => {
this.$vux.loading.hide()
}).catch(() => {
this.$vux.loading.hide()
})
/* if (Object.keys(this.$store.getters['user/userInfo']).length === 0) {
this.$vux.loading.show({
text: '正在加载'
})
this.getUserInfo().then((res) => {
this.$vux.loading.hide()
}).catch(() => {
this.$vux.loading.hide()
})
} */
},
computed: {
...mapGetters('user', [
'userInfo'
])
},
methods: {
...mapActions('user', [
'getUserInfo'
])
}
}



